import Vue from 'vue'
import Router from 'vue-router'
// 
import loginComponents from '@/components/login'
import main from '@/components/main'
// 
import dashboardComponent from '@/components/dashboard'
import accountComponent from '@/components/account/accountlist'
// 
import bannerComponent from '@/components/banner/bannerlist'
import advertisementComponent from '@/components/banner/advertisement'
// 
import drugsComponent from '@/components/drugs/drugslist'
// 
import orderComponent from '@/components/order/orderlist'
// 
import newsComponent from '@/components/news/newslist'
import drugshareComponent from '@/components/news/drugshare'
// 
import ruleComponent from '@/components/system/rule'
import shareComponent from '@/components/system/share'
import setupComponent from '@/components/system/setup'
import messageComponent from '@/components/system/message'
import integralComponent from '@/components/system/integral'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: loginComponents
    },
    {
      path: '/main',
      name: 'main',
      component: main
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      component: main,
      children: [{
        path: '/dashboard',
        name: 'dashboard',
        component: dashboardComponent
      }]
    }, {
      path: '/account',
      component: main,
      children: [{
        path: '/account/accountlist',
        name: 'accountlist',
        component: accountComponent
      }]
    },
    {
      path: '/banner',
      component: main,
      children: [{
        path: '/banner/bannerlist',
        name: 'bannerlist',
        component: bannerComponent
      }, {
        path: '/banner/advertisement',
        name: 'advertisement',
        component: advertisementComponent
      }]
    }, {
      path: '/drugs',
      component: main,
      children: [{
        path: '/drugs/drugslist',
        name: 'drugslist',
        component: drugsComponent
      }]
    }, {
      path: '/order',
      component: main,
      children: [{
        path: '/order/orderlist',
        name: 'orderlist',
        component: orderComponent
      }]
    }, {
      path: '/news',
      component: main,
      children: [{
        path: '/news/newslist',
        name: 'newslist',
        component: newsComponent
      }, {
        path: '/news/drugshare',
        name: 'drugshare',
        component: drugshareComponent
      }]
    }, {
      path: '/system',
      component: main,
      children: [{
        path: '/system/rule',
        name: 'rule',
        component: ruleComponent
      }, {
        path: '/system/share',
        name: 'share',
        component: shareComponent
      }, {
        path: '/system/setup',
        name: 'setup',
        component: setupComponent
      }, {
        path: '/system/message',
        name: 'message',
        component: messageComponent
      }, {
        path: '/system/integral',
        name: 'integral',
        component: integralComponent
      }]
    }
  ]
})



